<template>
  <div class="page-card-content">
    <div class="main-area">
      <div class="content-left">
        <div class="userlist">
          <img src="../../assets/img/Group 158.png" />
          <span>{{ centerList.account }}</span>
          <i class="el-icon-edit"></i>
          <div @click="editorPass()">修改密码</div>
        </div>
        <div class="loginlist">上次登录时间：{{ centerList.lastLoginTime }}</div>
        <div class="loginlist">上次登录IP：{{ centerList.lastLoginIp }}</div>
      </div>
      <div class="content-right" v-loading="loading">
        <div class="platform pl24">所属平台</div>
        <div class="list">
          <div
            class="plat-item"
            v-for="(item, index) in platformList"
            :key="index"
            :class="index == clickIndex ? 'active' : ''"
            @click="getMenu(item.appkey, index, item.url)"
          >
            <div>
              <img src="../../assets/img/Group 1649.png" />
            </div>
            <span> {{ item.name }}</span>
            <h4>{{ item.url }}</h4>
          </div>
        </div>
      </div>
    </div>
    <!-- 修改密码-->
    <el-dialog
      title="修改密码"
      width="600px"
      :visible.sync="dialogVisible"
      :close-on-click-modal="false"
      label-width="120px"
      style="margin-top:-20px"
    >
      <el-form ref="addForm" :model="addForm" label-width="100px" :rules="formRules">
        <el-form-item label="原密码" prop="oldPassword">
          <el-input type="password" v-model="addForm.oldPassword" class="labelWidth"></el-input>
        </el-form-item>
        <el-form-item label="新密码" prop="newPassword">
          <el-input type="password" v-model="addForm.newPassword" class="labelWidth"></el-input>
        </el-form-item>
        <el-form-item label="确认新密码" prop="sureNewPassword">
          <el-input type="password" v-model="addForm.sureNewPassword" class="labelWidth"></el-input>
        </el-form-item>
        <el-form-item>
          <el-button type="primary" @click="submitForm" style="float: right;">
            保存
          </el-button>
          <el-button
            type="primary"
            @click="submitForm(1)"
            plain
            style="float: right;margin-right:15px"
          >
            取消
          </el-button>
        </el-form-item>
      </el-form>
    </el-dialog>
  </div>
</template>

<script>
import API from '@/api/index.js'

export default {
  filters: {},
  data() {
    var validatePass = (rule, value, callback) => {
      if (value === '') {
        callback(new Error('请输入密码'))
      } else {
        if (this.addForm.sureNewPassword !== '') {
          this.$refs.addForm.validateField('sureNewPassword')
        }
        callback()
      }
    }
    var validatePass2 = (rule, value, callback) => {
      if (value === '') {
        callback(new Error('请再次输入密码'))
      } else if (value !== this.addForm.newPassword) {
        callback(new Error('两次输入密码不一致!'))
      } else {
        callback()
      }
    }
    return {
      loading: false,
      clickIndex: null,
      platformIcon: [
        {
          icon: require('../../assets/img/Group 1649.png')
        },
        {
          icon: require('../../assets/img/Group1646.png')
        },
        {
          icon: require('../../assets/img/Subtract.png')
        },
        {
          icon: require('../../assets/img/Group 1650.png')
        },
        {
          icon: require('../../assets/img/Vector (3).png')
        }
      ],
      centerList: {},
      platformList: [],
      dialogVisible: false,
      addForm: {
        newPassword: '',
        oldPassword: '',
        sureNewPassword: ''
      },
      formRules: {
        oldPassword: [
          {
            trigger: 'blur',
            message: '请输入原密码',
            pattern: '^[a-zA-Z0-9]',
            required: true
          }
        ],
        newPassword: [{ validator: validatePass, trigger: 'blur', required: true }],
        sureNewPassword: [{ validator: validatePass2, trigger: 'blur', required: true }]
      }
    }
  },
  created() {},
  mounted() {
    this.getCenter()
  },
  methods: {
    submitForm(type) {
      if (type == 1) {
        this.$refs.addForm.resetFields()
        this.dialogVisible = false
      } else {
        this.$refs.addForm.validate(valid => {
          if (valid) {
            const form = JSON.parse(JSON.stringify(this.addForm))
            delete form.sureNewPassword
            API.getAccountEditor(form).then(res => {
              if (res.status == 200) {
                this.dialogVisible = false
                this.$refs.addForm.resetFields()
                this.$message.success(res.message)
              } else {
                this.$message.error(res.message)
              }
            })
          } else {
            this.$message.error('请检查表单内容是否填写完整')
          }
        })
      }
    },
    editorPass() {
      this.dialogVisible = true
    },
    getMenu(appkey, index, url) {
      url = url + '?appkey=' + appkey
      this.clickIndex = index
      window.open(url)
    },
    getCenter() {
      this.loading = true
      API.getAccoutCenter()
        .then(res => {
          if (res.status == 200) {
            localStorage.setItem('username', res.data.account)
            this.centerList = res.data
            this.platformList = res.data.platforms
            if (this.platformList.length > 0) {
              this.platformList.forEach((item, index) => {
                item.icon = this.platformIcon[index % 5].icon
              })
            }
          } else {
            this.$message.error(res.message)
          }
        })
        .finally(() => {
          this.loading = false
        })
    }
  }
}
</script>

<style lang="scss" scoped>
.active {
  background: #ffffff;
  box-shadow: 0px 8px 30px rgba(0, 0, 0, 0.059);
  border-radius: 20px;
}
.main-area {
  display: flex;
  .content-left {
    width: 352px;
    height: 714px;
    border: 1px solid #eaebf0;
    padding: 40px 30px;
    margin-right: 40px;
    overflow: auto;
    .userlist {
      display: flex;
      margin-bottom: 23px;
      img {
        width: 60px;
        height: 60px;
        margin-right: 24px;
      }
      span {
        font-family: 'PingFang SC';
        font-style: normal;
        font-weight: bold;
        font-size: 20px;
        color: #0a1030;
        margin: auto 0px;
      }
      i {
        margin: auto 120px;
        margin-right: 0px;
      }
      div {
        margin: auto 0px;
        cursor: pointer;
      }
    }
    .loginlist {
      width: 292px;
      height: 32px;
      line-height: 32px;
      padding: 0px 16px;
      background: rgba(247, 248, 251, 0.8);
      border-radius: 4px;
      font-size: 14px;
      color: #2f2f2f;
      margin-bottom: 16px;
    }
  }
}
.content-right {
  flex: 1;
  border: 1px solid #eaebf0;
  margin-right: 24px;
  .list {
    height: 722px;
    padding: 10px;
    overflow: auto;
  }
  .plat-item {
    padding: 26px 0px;
    font-family: 'PingFang SC';
    font-style: normal;
    font-weight: bold;
    font-size: 15px;
    color: #2f2f2f;
    display: flex;
    align-items: center;
    cursor: pointer;
    margin-left: 26px;
    margin-bottom: 10px;
    border-bottom: 1px solid #e9ebf0;
    &:hover {
      background: #ffffff;
      box-shadow: 0px 8px 30px rgba(0, 0, 0, 0.059);
      border-radius: 20px;
      color: #2160ec;
    }

    div {
      width: 40px;
      height: 26px;
      background-image: url(../../assets/img/Group1645.png);
      background-size: 100% 100%;
      padding: 5px 13px;
      box-sizing: border-box;
      margin-right: 32px;
      margin-left: 20px;
      img {
        width: 13px;
        height: 13px;
        display: inline-block;
        margin-top: -5px;
      }
    }
    span {
      font-size: 15px;
      color: #2f2f2f;
      margin: auto 0px;
    }
    h4 {
      font-weight: 400;
      font-size: 15px;
      color: #6d6f7a;
      margin: auto 48px;
    }
  }
}

.pl24 {
  padding-left: 24px;
}
.platform {
  height: 42px;
  line-height: 42px;
  background: #f7f8fb;
  font-size: 15px;
  color: #2f2f2f;
  padding-left: 32px;
}
</style>
